}

.ans-radio {
  color: #4a4b4b;
  font-weight: normal;
  padding-right: 10px;
  vertical-align: middle;
}

.ans-radio {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 1em;
  padding: 0;
  font-size: 13px;
}

.ans-label {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  /*margin-bottom: 1em;*/
  padding: 0;
  font-size: 13px;
}

.checkbox_box .ans-label input[type="checkbox"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 0.7em;
  margin-left: 0;
}

.checkbox_box .ans-label input[type="checkbox"] {
  position: relative;
  width: 21px;
  height: 20px;
  border: 1px solid #515151;
  border-radius: 2px;
  background-color: #1E90FF;
  margin-bottom: 1em;
}

.checkbox_box .ans-label input[type="checkbox"]::after {
  content: '';
  width: 70%;
  height: 45%;
  border-radius: 0;
  margin: auto;
  position: absolute;
  top: 3px;
  left: 3px;
  -webkit-transition: background 0.25s ease;
  transition: background 0.25s ease;
}

.checkbox_box .ans-label input[type="checkbox"]:checked {
  background-color: #515151;
}

.checkbox_box .ans-label input[type="checkbox"]:checked::after {
  border-left: 3px solid;
  border-bottom: 3px solid;
  border-color: #1E90FF;
  border-radius: 0;
  transform: rotate(-45deg) translate3d(0, 0, 0);
  transform-origin: center center;
}

/* IE 10+ */
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .checkbox_box .ans-label input[type="checkbox"] {
    border: none;
  }
  .checkbox_box .ans-label input[type="checkbox"] {
    background-color: transparent;
  }
}

