.top_menu {
	padding: 0;
	margin: 0;
	height: 64px;
	margin-left: auto;
}

.top_menu ul li {
	display: inline-block;
	position: relative;
}

.top_menu ul li ul {
	background: #222533;
	padding: 5px;
	transition: all .5s ease-in-out;
	text-transform: none;
	opacity: 0;
	visibility: hidden;
	position: absolute;
	left: 0;
	top: 57px;
	margin-top: 20px;
	height: auto;
	min-width: 280px;
	width: 100%;
}

.container_fluid {
	padding-right: 15px;
	padding-left: 15px;
}

.top_menu ul li:hover>ul {
	opacity: 1;
	visibility: visible;
	transition: margin 0.3s ease 0s;
	margin: 0;
	z-index: 11;
}

.top_menu ul li ul li {
	position: relative;
	left: 0;
	display: block;
	height: auto;
	text-align: left;
}

.top_menu ul li ul li ul {
	top: 0;
	left: 90%;
	margin: 0 0 0 20px;
	/*IE6 only*/
}

.top_menu ul li ul li a {
	display: block;
	color: #f8f3f3;
	padding-left: 15px;
	border: none;
	font-size: 0.75rem;
	line-height: 1.33333;
	text-transform: uppercase;
	transition: all 0.2s ease;
}

.top_menu ul li ul li a:hover {
	color: #222533;
	background-color: #1089d4;
	-webkit-transition: all 0.2s ease;
	-o-transition: all 0.2s ease;
	transition: all 0.2s ease;
}

.menu-item-has-children a:after,
.top_menu>ul>li.menu-item-has-children>a::after {
	font-family: 'FontAwesome';
	content: "\f103";
	display: inline-block;
	// vertical-align: top;
	margin: 0px 0 0 8px;
	font-size: 1.0rem;
}

.menu-item-has-children ul li a:after {
	display: none;
}

.top_menu ul ul li:first-child>a {
	border-radius: 3px 3px 0 0;
}

.top_menu>ul>li.parent:hover>a::before {
	visibility: visible;
	transition: all .5s ease .2s;
}

.top_menu ul li a {
	display: block;
	text-decoration: none;
	text-transform: uppercase;
	color: #f8f3f3;
	padding: 10px 10px;
	font-size: 0.9375rem;
	line-height: 2.66667;
	transition: all 0.2s ease;
	color: #000000;
	font-size: .8rem;
	padding: 10px 20px;
	text-transform: uppercase;
	text-decoration: none;
}

.top_menu ul li a:hover {
	color: lighten(#000000, 10%);

}











