.btn {
	position: relative;
	display: block;
	overflow: hidden;
	// width: 100%;
	// height: 80px;
	// max-width: 250px;
	margin: 1rem auto;
	text-transform: uppercase;
	font-size: 18px;
	line-height: 22px;
	font-weight: 600;
	text-decoration: none;
	background-color: transparent;
	border: 1px solid #1089d4;
	border-radius: 10px;
	padding: 20px 30px;
	display: inline-block;
	transition: all 0.4s ease-in-out;
}

.btn:before,
.btn:after {
	content: "";
	position: absolute;
	top: 0;
	left: 0;
	width: 0;
	height: 80px;
	background-color: #017df7;
	transition: 0.4s;
	z-index: 10;
}

.btn span {
	width: 100%;
	position: relative;
	//	display: block;
	top: 0;
	left: 0;
	color: #017df7;
	z-index: 1000;
}


.btn:after {
	left: auto;
	right: 0;
}

.btn:hover span {
	color: #fff;
}


.btn:hover:before,
.btn:hover:after {
	width: 250px;
}

.btn:active {
	background-color: darken(#017df7, 10%);
}




