	display: none;
	position: absolute;
	top: 30px;
	right: 0;
}

.mobile_minu_block nav {
	line-height: 1.4;
	background: #1d1f20;
	padding: 25px 35px;
	width: 300px;
	top: calc(50vh - 247px);
	left: calc(50vw - 150px);
	//position: fixed;
	border-radius: 16px;
	box-shadow: 0 0 60px -15px black;
	transform: scale(0) rotate(360deg);
	transition: all 0.75s;
	font-size: 18px;
	z-index: 1000;
	top: 0;
}

.mobile_minu_block nav.menu_active {
	transform: scale(1) rotate(0deg);
}

.mobile_minu_block nav>ul>li {
	margin: 20px 0;
	color: #b5bc67;
}

// .mobile_minu_block nav > ul > li:first-of-type {
//   color: #efc371;
// }

.mobile_minu_block nav>ul>li>ul {
	margin: 10px 0 0 20px;
	border-left: 1px solid #4d4d4c;
	color: #ae94c0;
	list-style: none;
}

.mobile_minu_block nav>ul>li>ul>li {
	margin: 7px 0;
	padding-left: 20px;
}

.mobile_minu_block nav a:hover {
	color: #dd925f;
}

.mobile_minu_block h3 {
	// position: fixed;
	position: absolute;
	display: inline-block;
	top: 0;
	font-size: 20px;
	color: #cfd8dc;
	top: calc(50vh - 40px);
	left: calc(50vw - 126px);
	cursor: pointer;
	transition: all 0.3s;
	top: 0;
	right: 0;
}

.mobile_minu_block h3:hover {

	padding-bottom: 10px;
	// top: calc(50vh - 50px);
}

.mobile_minu_block p {
	float: right;
	cursor: pointer;
	font-size: 16px;
	color: #cfd8dc;
	transition: all 0.2s;
}

.mobile_minu_block p:hover {
	border-bottom: 2px dotted #cfd8dc;
	padding-bottom: 5px;
}

.mobile_minu_block ul li a {
	color: #fff;
	text-decoration: none;
}
